* {
  box-sizing: border-box; }

body {
  font-family: Open Sans;
  font-weight: bold;
  font-size: 16px;
  font-weight: normal;
  line-height: 150%;
  letter-spacing: normal;
  text-transform: none;
  color: #000000;
  padding: 20px; }

ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }

h1,
h2,
h3,
p {
  margin: 0;
  padding: 0; }

a {
  color: inherit;
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  object-fit: cover; }

button,
input {
  margin: 0;
  padding: 0;
  color: inherit;
  border: none;
  outline: none;
  background-color: transparent; }

button {
  cursor: pointer; }

@media screen and (max-width: 1024px) {
  body {
    font-family: Open Sans;
    font-weight: bold;
    font-size: 14px;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: normal;
    text-transform: none; } }

/* The Modal (background) */
.modal {
  display: none;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  padding-top: 100px;
  /* Location of the box */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */ }

.modal__content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 300px;
  box-shadow: 10px 10px 10px #747474; }

/* The Close Button */
.modal__close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold; }

.modal__close:hover,
.modal__close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer; }

.modal__submit {
  border: 1px solid #ccc;
  background-color: #2771fc;
  color: #fff;
  padding: 20px; }

.modal__submit:hover {
  cursor: pointer;
  background-color: #5690fc; }

.open {
  border: 1px solid #ccc;
  background-color: #2771fc;
  color: #fff;
  padding: 20px; }

.open:hover {
  cursor: pointer;
  background-color: #5690fc; }

.list {
  padding-bottom: 20px; }

.deal__container {
  display: block; }

.deal__name {
  padding-top: 20px;
  padding-bottom: 10px;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 1px;
  font-family: system-ui; }
